<template>
  <div id="header">
    <div id="avatar">
      <a href="http://localhost:8080/#/">
        <img src="../../assets/avatar.jpg" >
      </a>
    </div>
    <barMenu text="文本" @message='getMEssage'></barMenu>
  </div>
</template>

<script>
  import barMenu from '@/components/views/Bar'
  export default{
    name: "Header",
    components:{
      barMenu
    },
    data (){
      return {

      }
    },
    methods:{
      getMEssage(val){
        alert(val);
      }
    }
  }
</script>

<style>
  #header {
    margin-top: 10px;
    width: auto;
    height: auto;
  }
  #avatar img {
    width: 70px;
    height: 70px;
    border: 3px solid #868686;
    box-sizing: border-box;
    border-radius: 35px;
  }
</style>



